import React,{Component} from 'react';
import firestore from './firestore';
import firebase,{auth} from 'firebase';
import { MDBContainer, MDBRow, MDBCol, MDBCard, MDBCardBody, MDBInput, MDBBtn, MDBIcon } from 'mdbreact';
import ReactApexChart from 'react-apexcharts';

class Show extends Component
{
   constructor(props)
   {   
       super(props);
       this.state={
        prev:"",
        series: [{
            name: 'Name',
            data: [],
            dates:[],
            
          }],
          options: {
            chart: {
              type: 'area',
              stacked: false,
              height: 350,
              
              zoom: {
                type: 'x',
                enabled: true,
                autoScaleYaxis: true
              },
              toolbar: {
                autoSelected: 'zoom'
              }
            },
            dataLabels: {
              enabled: false
            },
            markers: {
              size: 0,
            },
            title: {
              text: 'Stock Price Movement',
              align: 'left'
            },
            fill: {
              type: 'gradient',
              gradient: {
                shadeIntensity: 1,
                inverseColors: false,
                opacityFrom: 0.5,
                opacityTo: 0,
                stops: [0, 90, 100]
              },
            },
            yaxis: {
              labels: {
                formatter: function (val) {
                  return (val / 1000000).toFixed(0);
                },
              },
              title: {
                text: 'Price'
              },
            },
            xaxis: {
              categories: []
            }
           
          }
        

       }
   }
  
   componentDidMount()
   {
    const pointer=this;
    const API_key='HGJWFG4NBAQ66ICD';
   
    
    var link=`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${this.props.showGraph}&outputsize=compact&apikey=${API_key}`;
    var arrayX=[];
    var arrayY=[];

    fetch(link)
    .then(function(response)
    {
        return response.json();
    })
    .then(function(data)
    {   
        console.log(data);
        var key;
        for( key in data[`Time Series (Daily)`])
        {  
            arrayX.push(key);
            arrayY.push(data[`Time Series (Daily)`][key][`1. open`]);
        }
        for( key in data[`Time Series (Daily)`])
        { var y=data[`Time Series (Daily)`][key][`1. open`];
          var z=data[`Time Series (Daily)`][key][`4. close`]
          break;
        }
       
        let series1 = JSON.parse(JSON.stringify(pointer.state.series));
        let options1 = JSON.parse(JSON.stringify(pointer.state.options));
        series1[0].data = arrayY.reverse();
        series1[0].name=pointer.props.showGraph;
        options1.xaxis.categories=arrayX.reverse();
          pointer.setState({
            series:series1
          }) ;
          pointer.setState({
            options:options1,

          }) ;

        
        if(pointer.props.showGraph!="")
        {
        document.getElementById("1").textContent=key;
        document.getElementById("2").textContent=y;
        document.getElementById("3").textContent=z;
        }
        
        
    });
      
   
       

   }
   componentDidUpdate()
   {if(this.state.prev!=this.props.showGraph)
    {
     
    const pointer=this;
    const API_key='HGJWFG4NBAQ66ICD';
   
    
    var link=`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${this.props.showGraph}&outputsize=compact&apikey=${API_key}`;
    var arrayX=[];
    var arrayY=[];

    fetch(link)
    .then(function(response)
    {
        return response.json();
    })
    .then(function(data)
    {   
        console.log(data);
        var key;
        for( key in data[`Time Series (Daily)`])
        {
            arrayX.push(key);
            arrayY.push(data[`Time Series (Daily)`][key][`1. open`]);
        }
        for( key in data[`Time Series (Daily)`])
        { var y=data[`Time Series (Daily)`][key][`1. open`];
          var z=data[`Time Series (Daily)`][key][`4. close`]
          break;
        }
       
        let series1 = JSON.parse(JSON.stringify(pointer.state.series));
        let options1 = JSON.parse(JSON.stringify(pointer.state.options));
        series1[0].data = arrayY.reverse();
        series1[0].name=pointer.props.showGraph;
        options1.xaxis.categories=arrayX.reverse();
          pointer.setState({
            series:series1
          }) ;
          pointer.setState({
            options:options1

          });

        
        
        document.getElementById("1").textContent=key;
        document.getElementById("2").textContent=y;
        document.getElementById("3").textContent=z;
        
        
        
    });
    this.setState({prev:this.props.showGraph});
   }
      
   }
   
render(){
    return(
       <div>
           <ReactApexChart  options={this.state.options} series={this.state.series} type="area" height={300} /> 
            <h3>Last working day stats:</h3><br/>
            <table class="table table-striped">            
              <th scope="col" ><strong>Date</strong>:</th>
              <th scope="col" id="1"></th>
              <th>&nbsp;&nbsp;&nbsp;</th>
              
              <th scope="col"><strong>Open at:</strong></th>
              <th scope="col"id="2"></th>
              <th>&nbsp;&nbsp;&nbsp;</th>
            
              <th scope="col"><strong>Closed at:</strong></th>
              <th scope="col"id="3"></th>
              <th>&nbsp;&nbsp;&nbsp;</th>
           
            
            </table>
      
       </div>
    );
}

}
export default Show;