import React,{Component,useState, useEffect} from 'react';
import firestore from './firestore';
import firebase,{auth} from 'firebase';
import Graph from './graph';
import Signout from './signout';
import History from './history';
import Invest from './invested';
import {Popover,OverlayTrigger} from 'react-bootstrap'
import Home from './home'

const Main=()=> {
 var [isOpen,toggleCollapse]=useState(false);
 var [rend,rerend]=useState(0);
 const funct=(e)=>
 {
   rerend(e);
 }
  
  useEffect(()=>
 { 
   const db=firebase.firestore();
   if(firebase.auth().currentUser!=null)
   {
   db.collection("user").doc(firebase.auth().currentUser.uid).get()
   .then(function(doc)
   { 
      
    var c=doc.data().balance;
    
    rerend(c);
   })
   }

 })


 toggleCollapse = () => {
  isOpen=!isOpen;
}
var [curr,choose]=useState(1);
const popover = (
  <Popover id="popover-basic">
    
    <Popover.Content>
      click to logout
    </Popover.Content>
  </Popover>
);



  return (
    <div>
       <nav class="mb-1 navbar navbar-expand-lg navbar-dark " style={{ background: "linear-gradient(to right, #009933 0%, #00ff00 100%)"}}>
        <a class="navbar-brand" href="#"><strong>Let's Invest</strong></a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent-333"
          aria-controls="navbarSupportedContent-333" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent-333">
          <ul class="navbar-nav mr-auto">
            <li class="nav-item active">
              <a class="nav-link" href="#" value={curr} onClick={e=>choose(1)}>Home
               
              </a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#"value={curr} onClick={e=>choose(2)}>Invest</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#"value={curr} onClick={e=>choose(6)}>my current stocks</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#"value={curr} onClick={e=>choose(3)}>My History</a>
            </li>
            
          </ul>
          <ul class="navbar-nav ml-auto nav-flex-icons">
            <li class="nav-item">
            <a class="nav-link waves-effect waves-light" >
             ₹ &nbsp;{rend}
            </a>
            
            </li>
            <li class="nav-item"onClick={e=>choose(4)}>
            <OverlayTrigger trigger="hover" placement="bottom" overlay={popover}>
              <a class="nav-link waves-effect waves-light" >
              <i class="fa fa-user" aria-hidden="true"></i>
              </a>
            </OverlayTrigger>
            </li>
           
          </ul>
        </div>
      </nav>
     
    <div>
   
    {curr==2?<Graph funct={funct}/>:null}
    {curr==3?<History/>:null}
    {curr==4?<Signout/>:null}
    
    {curr==6?<Invest  funct={funct}/>:null}
    {curr==1?<Home/>:null}
    </div>
    </div>
    
    
    );
  }


export default Main;