import React,{Component} from 'react';
import firestore from './firestore';
import firebase,{auth} from 'firebase';
import './graph.css';
import { MDBContainer, MDBRow, MDBCol, MDBCard, MDBCardBody, MDBInput, MDBBtn, MDBIcon } from 'mdbreact';
import Show from './show';

class Graph extends Component
 {  constructor(props) {
    super(props);
    
    
    this.state = {
      sname:"",
      call:0,
      uid:"",
      money:0,y:0
     
    
    };
  }
  componentDidMount=()=>
  { var user=firebase.auth().currentUser;
   
   
    {this.setState({uid:user.uid});}
    
    

  }
    
    
    puts=()=>
    {   this.setState({sname:document.getElementById("form1").value});
        
    }   
    invest=()=>
    {   this.setState({sname:document.getElementById("form1").value});
      if(this.state.sname!="")
        {
        const pointer=this;
        this.setState({money:document.getElementById("money").value});
        if(document.getElementById("money").value!="")
        {
        const API_key='HGJWFG4NBAQ66ICD';
        var link=`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${this.state.sname}&outputsize=compact&apikey=${API_key}`;
        fetch(link)
        .then(function(response)
        {
          return response.json();
        }
        )
        .then(function(data)
        {
          var key;
          for( key in data[`Time Series (Daily)`])
          { 
             var y=data[`Time Series (Daily)`][key][`1. open`];
            break;
          }
          pointer.setState({y:y});
        var db=firebase.firestore();
        var c=0;
        db.collection("user").doc(firebase.auth().currentUser.uid).get()
          .then(function(doc)
          { 
             c=doc.data().balance;
            
          }
          )
          .then(function()
          {
          if(c-pointer.state.money<0)
          alert("Insufficient balance");
          else
          { db.collection("user").doc(pointer.state.uid).update({balance:c-pointer.state.money})
            db.collection("user").doc(pointer.state.uid).collection("history").add(
            {
                date:key,
                StockName:pointer.state.sname,
                money:pointer.state.money,
                StockRate:pointer.state.y,
                exist:true 

            })
          }
          
          pointer.props.funct(c-pointer.state.money);
        })
        alert("invested ₹"+pointer.state.money+" in "+pointer.state.sname)
        
        })
        }
        else
        {
           alert("Enter money");   
        }  
      }

      else
      alert("click on search button first");
    }
    
    render()
    {
    return(
        <div>
            <br/><br/><br/>
        
        <div class="row">
            
            <div class="col-sm-1"></div>
            <div id="chart"class="col-sm-7">
            <Show showGraph={this.state.sname}/>
            
            </div>
            
           
            <div class="col-sm-3">
            <h3>Enter Stock code:</h3>
            <input type="text" mdbInput type="text" id="form1"class="form-control" style={{width:200}}/>
            
            < MDBBtn  onClick={()=>this.puts()}  style={{ background: "linear-gradient(to right, #009933 0%, #00ff00 100%)", color:"#ffffff"}}><i class="fa fa-search"></i></MDBBtn><br/><br/>
            <h3>Invest Money?</h3><br/>
            <input type="text" mdbInput type="text" id="money" class="form-control"  placeholder="enter in Rupees" style={{width:200}}/>
            <MDBBtn onClick={()=>this.invest()} style={{ background: "linear-gradient(to right, #009933 0%, #00ff00 100%)" ,color:'#ffffff'}}>Invest</MDBBtn><br/>
             
            </div>
            <div class="col-sm-1">
            </div>
            
            
            
           
      
            
        </div>
        </div>
    );
    }
 }
export default Graph;