import React,{Component} from 'react';
import {Carousel} from 'react-bootstrap';
import stock1 from './stock3.jpg'
import stock2 from './stock4.jpg'
import stock3 from './stock5.jpg'
import {Card,Button} from 'react-bootstrap'
class Home extends Component
{   constructor(props)
    {super(props) }
  
    render(){
        return(
            <div >
                
                <div class="row" style={{marginTop:"20px"}} >
                <div class="col-md-2">
                </div>
                <div class="col-md-8">
                <Carousel >
                    <Carousel.Item>
                        <img
                        className="d-block w-100"
                        src={stock1}
                  
                        alt="First slide"
                        />
                        <Carousel.Caption>
                        <h4>Practice with investing money</h4>
                      
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img 
                        className="d-block w-100"
                        src={stock3}
                        alt="seconslide"
                        />

                        <Carousel.Caption>
                        <h4>Easy to operate</h4>
                        
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                          className="d-block w-100"
                          src={stock2}
                          alt="Third slide"
                        />

                        <Carousel.Caption>
                        <h4>Works on real World Stock market</h4>
                        
                        </Carousel.Caption>
                    </Carousel.Item>
                    </Carousel>
                    </div>
                    <div class="col-md-2"></div>
                    </div>
                    <center><h4 style={{color:"#42d728", marginTop:"40px"}}><u>Overview</u></h4></center>
                    <div class="row" style={{marginBottom:"100px"}}>
                        
                        <div class="col-md-4">
                        <center>
                        <Card style={{ width: '18rem',height:"150px" }}>
                        
                        <Card.Body style={{backgroundColor:"#42d728",color:"#ffffff"}}>
                            <Card.Title>Invest</Card.Title>
                            <Card.Text>
                            Invest money on various stocks part NASDAQ
                            and view data of previous 3 months.
                            
                            </Card.Text>
                            
                        </Card.Body>
                        </Card>
                        </center>
                        </div>
                        
                        
                        <div class="col-md-4"><center>
                        <Card style={{ width: '18rem',height:"150px" }}>
                       
                        <Card.Body style={{backgroundColor:"#42d728",color:"#ffffff"}}>
                            <Card.Title>My current Stocks</Card.Title>
                            <Card.Text>
                             View your investment and how they are performing.
                             sell your shares whenever you want.
                            </Card.Text>
                            
                        </Card.Body>
                        </Card>
                        </center> </div>
                        
                        
                        <div class="col-md-4"><center>
                        <Card style={{ width: '18rem',height:"150px"}}>
                       
                        <Card.Body style={{backgroundColor:"#42d728",color:"#ffffff"}}>
                            <Card.Title>History</Card.Title>
                            <Card.Text>
                            Watch history of yours investments with imformation.
                            </Card.Text>
                          
                        </Card.Body>
                        </Card></center>
                        </div>
                        
                    </div>
                    
            </div>

        )
    }
}
export default Home;