import React,{Component,useEffect,useState} from 'react';
import firestore from './firestore';
import firebase,{auth} from 'firebase';
import {Button,Modal,Row,Col} from 'react-bootstrap'
class MyVerticallyCenteredModal extends Component {
   
    constructor(props)
    {
        super(props);
        this.state={sata:null,rate:0,myid:null,profit:0,totol:0};
    }
    
    componentDidMount()
    {   if(this.props.myid!=null)
        {   const db=firebase.firestore();
            const pointer=this;
            db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").doc(this.props.myid).get()
            .then(function(doc){
            var sata=doc.data();
            pointer.setState({sata:sata});
            })
            .then(function(){
            const API_key='HGJWFG4NBAQ66ICD';
            var link=`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${pointer.state.sata.StockName}&outputsize=compact&apikey=${API_key}`;
            fetch(link)
            .then(function(response)
            {
                return response.json();
            })
            .then(function(data)
            {   
               
                var key;
                
                for( key in data[`Time Series (Daily)`])
                { var rate=(data[`Time Series (Daily)`][key][`4. close`]);
                  break;       
                }
               pointer.setState({rate:rate})
               var profit=0;

                   
                 profit=(rate-pointer.state.sata.StockRate)*(pointer.state.sata.money/pointer.state.sata.StockRate);
                 profit=profit.toFixed(0);
                 var total=parseFloat(profit)+parseFloat(pointer.state.sata.money);  
                 total=total.toFixed(0);
                pointer.setState({profit:profit,total:total});
                 

            })
        })}
    
    }
    componentDidUpdate()
    {
        if(this.props.myid!=this.state.myid)
        {

            if(this.props.myid!=null)
            {   const db=firebase.firestore();
                const pointer=this;
                db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").doc(this.props.myid).get()
                .then(function(doc){
                var sata=doc.data();
                pointer.setState({sata:sata});
                })
                .then(function(){
                const API_key='HGJWFG4NBAQ66ICD';
                var link=`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${pointer.state.sata.StockName}&outputsize=compact&apikey=${API_key}`;
                fetch(link)
                .then(function(response)
                {
                    return response.json();
                })
                .then(function(data)
                {   
                   
                    var key;
                    
                    for( key in data[`Time Series (Daily)`])
                    {   
                        var rate=(data[`Time Series (Daily)`][key][`1. open`]);
                      break;       
                    }
                   pointer.setState({rate:rate})
                   var profit=0;
                   
                   {
                       profit=(rate-pointer.state.sata.StockRate)*(pointer.state.sata.money/pointer.state.sata.StockRate);
                       profit=profit.toFixed(0);
                   }
                   var total=parseFloat(profit)+parseFloat(pointer.state.sata.money);
                   total=total.toFixed(0);
                   pointer.setState({profit:profit,total:total});
    
                })
            })}
        
            this.setState({myid:this.props.myid});
        }
    }
    funct=()=>
    {  var c=0,exist;
       const pointer=this;
       const db=firebase.firestore();
       db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").doc(pointer.props.myid).get()
     .then(function(doc)
     { 
        exist=doc.data().exist;
      
       
     })
     .then(function()
     {
       if(exist)
       {
     
       db.collection("user").doc(firebase.auth().currentUser.uid).get()
       .then(function(doc)
       {  c=doc.data().balance;         
       })
       .then(function(){
       
        db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").doc(pointer.props.myid)
        .update({exist:false});
        var ftotal=parseFloat(c)+parseFloat(pointer.state.total);
        
        db.collection("user").doc(firebase.auth().currentUser.uid).update({balance:ftotal});
        c=ftotal;
        pointer.props.change(c);
       
    })
    }
    else
    alert("Stock already sold");
})
}
    render()
    {

    return (
      <Modal
        {...this.props}  size="lg" aria-labelledby="contained-modal-title-vcenter"centered  >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
    <h3>Stock imformation:{this.state.sata!=null?this.state.sata.StockName:null}</h3>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="show-grid">
        
        <Row>
            <Col xs={6} >
            <h5>Date of Investment:</h5>
            </Col>
            <Col xs={6}>
            <strong>{this.state.sata!=null?this.state.sata.date:null}</strong> 
            </Col>
        </Row>
        <Row>
            <Col xs={6}>
             <h5>Rate when invested</h5>
            </Col>
            <Col xs={6}>
            <strong>{this.state.sata!=null?this.state.sata.StockRate:null}</strong> 
            </Col>
        </Row>
        <Row>
            <Col xs={6}>
             <h5>Money invested:</h5>
            </Col>
            <Col xs={6} >
            <strong>{this.state.sata!=null?this.state.sata.money:null}</strong> 
            </Col>
        </Row>
        <Row>
            <Col xs={6}>
             <h5>Current rate:</h5>
            </Col>
            <Col xs={6} >
            <strong>{this.state.rate!=null?this.state.rate:null}</strong> 
            </Col>
        </Row>
        <Row>
            <Col xs={6}>
             <h5>total profit</h5>
            </Col>
            <Col xs={6} >
            <strong>{this.state.profit!=null?this.state.profit:null}</strong> 
            </Col>
        </Row>
        <Row>
            <Col xs={6}>
             <h4> Total amount:</h4>
            </Col>
            <Col xs={6} >
            <h4>{this.state.total}</h4>
            </Col>   
        </Row>  
        </Modal.Body>
        <Modal.Footer>
        <Button onClick={()=>this.funct()}>Sell Stock</Button>
            
        <Button onClick={this.props.onHide}>Close</Button>
            
       
         
         
        </Modal.Footer>
      </Modal>
    );
  }
}

class Invest extends Component{ 
    constructor(props)
    {
        super(props);
        
        this.state={show:null,arr:[],modal:false,call:0,id:null}        
    }
    componentDidMount()
    {   const pointer=this;
        const db=firebase.firestore();
        db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").get()
        .then(snapshot=>{
            snapshot.forEach(

                doc=>{
                if(doc.data().exist==true)
                {
                    db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").doc(doc.id)
                    .update({
                        id:doc.id
                    })
                }
            })
        })
        db.collection("user").doc(firebase.auth().currentUser.uid).collection("history").get()
        .then(snapshot=>{
            const show=[];
           
            snapshot.forEach(

                doc=>{
                if(doc.data().exist==true)
                {
                const data=doc.data()
                show.push(data);
                pointer.setState({show:show})
                
     
        }
       }) 
    })
    
      
   
        
    }
    setModalShow=(e,id)=>
    {   
        this.setState({modal:e,id:id,call:1});
        
    }
    change=(e)=>
    {
        this.props.funct(e);
    }
    render()
    {
        return(
            <div>
            <table class="table table-striped"style={{ width:"1000px",marginLeft:"100px" ,marginTop:"100px",marginRight:"100px"}}>
            <th scope="col"><center>Stock Name</center></th>
            <th scope="col"><center>Stock value when invested</center></th>
            <th scope="col"><center>Date of purchase</center></th>
            <th scope="col"><center>Money invested(₹)</center></th>
            {  
                this.state.show&&
                this.state.show.map(show=>{
                    return(
                        <tr>  
                           
                            <td><center>{show.StockName}</center></td>
                            <td><center>{show.StockRate}</center></td>
                            <td><center>{show.date}</center></td>
                            <td><center>{show.money}</center></td>
                            <td><center> <Button variant="outline-success"size="sm" id={show.id} onClick={() => this.setModalShow(true,show.id)}>View</Button></center></td>
                            
                        </tr>

                    )
                })
            } 
            
        </table> 
        {this.state.call==1?<MyVerticallyCenteredModal myid={this.state.id} change={()=>this.change()} show={this.state.modal} onHide={() => this.setModalShow(false)} /> :null} 
            </div>
        )
    }
}
export default Invest;